* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fff;
  flex-direction: column;
  align-items: center;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  line-height: 1.5;
  display: flex;
}

.search-form {
  text-align: center;
  height: 65px;
  width: 100%;
  background-color: #fff;
  margin-bottom: 50px;
  padding-top: 15px;
  position: relative;
  box-shadow: 0 7px 29px rgba(100, 100, 111, .2);
}

.search_input {
  height: 35px;
  width: 300px;
  outline: rgba(0, 0, 0, 0);
}

.submit_btn {
  height: 35px;
  width: 45px;
  cursor: pointer;
  background-color: #afadad;
  border: none;
  position: absolute;
  right: 39%;
}

.load-more {
  height: 35px;
  cursor: pointer;
  background-color: #afadad;
  border: none;
  border-radius: 5px;
  align-items: center;
  margin-top: 30px;
  padding: 15px 30px;
  display: flex;
}

.hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.gallery {
  flex-wrap: wrap;
  justify-content: space-between;
  gap: 30px;
  display: flex;
}

.photo-card {
  flex-basis: calc(25% - 22.5px);
}

img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  display: block;
}

.info {
  border: .5px solid rgba(0, 0, 0, .24);
  border-top: none;
  justify-content: space-evenly;
  padding-bottom: 10px;
  padding-left: 35px;
  display: flex;
  box-shadow: 0 25px 20px -20px rgba(0, 0, 0, .45);
}

