* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: white;
  color: #212121;
  line-height: 1.5;
  display: flex;
  align-items: center;
  flex-direction: column;
}

.search-form {
  position: relative;
  text-align: center;
  height: 65px;
  padding-top: 15px;
  box-shadow: rgb(100 100 111 / 20%) 0px 7px 29px 0px;
  background-color: white;
  width: 100%;
  margin-bottom: 50px;
}
.search_input {
  height: 35px;
  width: 300px;
  outline: transparent;
}
.submit_btn {
  position: absolute;
  right: 39%;
  height: 35px;
  background-color: #afadad;
  border: none;
  width: 45px;
  cursor: pointer;
}
.load-more {
  background-color: #afadad;
  border: none;
  height: 35px;
  padding: 15px 30px;
  margin-top: 30px;
  border-radius: 5px;
  display: flex;
  align-items: center;
  cursor: pointer;
  margin-top: 30px;
}
.hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.gallery {
  display: flex;
justify-content: space-between;
gap: 30px;
 
  flex-wrap: wrap;
}
.photo-card {

   flex-basis: calc((100% - 90px )/ 4);
   
}
img {
     width: 100%;
    height: 100%;
    object-fit: cover;
    display: block;
}

.info {
  display: flex;
  justify-content: space-evenly;
  padding-bottom: 10px;
  padding-left: 35px;
  border: 0.5px solid #0000003d;
    border-top: none;
    box-shadow: rgba(0, 0, 0, 0.45) 0px 25px 20px -20px;
}
